
import Vue from 'vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(LoadingPlugin)
export default {
  data () {
    return {
    }
  },
  created (){
    // 
    this.$vux.loading.show({
    text: '...'
    })

    var _state='zhegewojuedemeishenmyong'//
    //token
    this.$http({
      method: 'Get',
      url: this.$store.state.domain + 'wechat/login',
      params: {'code': this.$route.params.code, 'state': _state}
    })
    .then((d) => {
        this.$store.state.jwt = {// token
        token: 'Bearer ' + d.data.access_token,
        expires_in: d.data.expires_in,
        createTime: new Date().getTime(),
        code: this.$route.params.code,
        state: _state
        }

        //
        this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/WechatAPI/IsBindCustomerPhone',
            body:{},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
                if(!d.data.success){//
                  this.$router.push("/bindmobile")
                }else{
                  if(d.data.content){
                    var _page=decodeURIComponent(this.$route.params.page).replace("/","")
                    this.$router.replace("/"+_page)//
                  }else{
                     this.$router.replace("/bindmobile")//
                  }
                }
                this.$vux.loading.hide()
              }, (d) => {
                alert('');
        });

    }, (d) => {
        alert('!')
    })
  }
}



